'use client'

import React from 'react'
import { Card, CardContent, CardTitle, CardHeader } from '@/components/ui/card'
import { Textarea } from '@/components/ui/textarea'
import UseProject from '@/hooks/use-project'
import { Button } from '@/components/ui/button'
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog'
import Image from 'next/image'
import { askQuestion } from './actions'
import { readStreamableValue } from 'ai/rsc'

const AskQuestionCard = () => {
  const { project } = UseProject()
  const [open, setOpen] = React.useState(false)
  const [question, setQuestion] = React.useState('')
  const [loading , setLoading] = React.useState(false)
  const [answer , setAnswer] = React.useState('')
  const [fileReferences , setFilesReferences] = React.useState<{ fileName :string ; sourceCode :string; summary:string }[]>([])


  const onSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    if (!project?.id) return

    setLoading(true)
    setOpen(true)

    try {
      const { output, fileReferences } = await askQuestion(question, project.id)
      setFilesReferences(fileReferences)

      for await (const delta of readStreamableValue(output)) {
        if (delta) {
          console.log('Delta:', delta);
          setAnswer((ans) => ans + delta)
        }
      }
    } catch (err) {
      console.error('Error while asking question:', err)
      setAnswer('An error occurred while getting the answer.')
    }
    setLoading(false)
  }
  return (
    <>
      <Dialog open={open} onOpenChange={setOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Image src="/logo.png" alt="githubai" width={40} height={40} />
            </DialogTitle>
          </DialogHeader>
          <div className="whitespace-pre-wrap mb-4">{answer}</div>
          <h1>File References</h1>
          {fileReferences.map(file =>{
            return <span key = {file.fileName}>{file.fileName}</span>
          })}
        </DialogContent>
      </Dialog>

      <Card className="relative col-span-3">
        <CardHeader>
          <CardTitle>Ask a question</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={onSubmit}>
            <Textarea
              placeholder="Explain the codebase"
              value={question}
              onChange={(e) => setQuestion(e.target.value)}
            />
            <div className="h-4" />
            <Button type="submit">Ask the AI</Button>
          </form>
        </CardContent>
      </Card>
    </>
  )
}

export default AskQuestionCard
